export default {
  name: 'langSelect',
  data () {
    return {
      language: ''
    }
  },
  mounted () {
    const _lang = localStorage.lang || 'cn'
    this.getLanguage(_lang)
  },
  methods: {
    handleSetLanguage (lang) {
      this.$i18n.locale = lang
      localStorage.setItem('lang', lang)
      this.getLanguage(lang)
    },
    getLanguage (val) {
      if (val === 'cn') {
        this.language = '中文'
      }
      if (val === 'en') {
        this.language = 'English'
      }
    }
  }
}



