  export default {
    name: 'tabNav',
    data () {
      return {
        rightMenuShow: false,
        left: 0,
        top: 0
      }
    },
		created(){
			console.log(this.$store.getters.addRouters)
			this.$store.getters.addRouters.forEach((item,index)=>{
				item.nav_id = index
				console.log(item)
			})
			console.log(this.$store.getters.addRouters,this.$store.getters.tabnavBox)
		},
    methods: {
      openMenu (item, e, index) {
				console.log(123,item)
        if (index === 0) {
          return false
        }
        this.rightMenuShow = true
        this.left = e.clientX + 10
        this.top = e.clientY
        this.$store.dispatch('openMenu', item)
      },
      removeTab (tabItem) {
        this.$store.dispatch('removeTab', {tabItem, fullPath: this.$route.fullPath, router: this.$router})
      },
      removeOtherTab (tabItem) {
				console.log(222,tabItem)
        this.$store.dispatch('removeOtherTab', {tabItem, router: this.$router})
      },
      removeAllTab () {
				console.log(3333,tabItem)
        this.$store.dispatch('removeOtherTab', {all: true, router: this.$router})
      }
    },
    watch: {
      rightMenuShow (value) {
        if (value) {
          document.body.addEventListener('click', () => {
            this.rightMenuShow = false
          })
        } else {
          document.body.removeEventListener('click', () => {
            this.rightMenuShow = false
          })
        }
      }
    }
  }



