import Vue from 'vue'
import Router from 'vue-router'
import router from '../router'
import CryptoJS from 'crypto-js'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)


function getCookie(name) {
let arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
if (arr = document.cookie.match(reg)){
return (arr[2]);
}else{
return null;
}
}

function setCookie (c_name, value) {
const expiredays = 1000 * 60 * 60 * 24 * 3;
let exdate = new Date();
exdate.setTime(exdate.getTime() + expiredays);
document.cookie = c_name + "=" + escape(value) + ((expiredays == null) ? "" : ";expires=" + exdate.toGMTString());
};

function delCookie (name) {
var exp = new Date();
exp.setTime(exp.getTime() - 1);
var cval = getCookie(name);
if (cval != null)
document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
};


function goback(status,info){
if(status == '-998' || status == '-999'){
delCookie("token");
Vue.$vux.toast.text(info,"middle");
setTimeout(()=>{
router.push({ path: '/login'})
},1000)
}else{
Vue.$vux.toast.show({
type: "text",
text: info,
position: "niddle",
width: "9em"
})
}
}
function gotoLogin(){
const token = JSON.parse(unescape(getCookie('token')));
if(!token){
Vue.$vux.toast.text("请先登录", "middle");
router.push({ path: 'login'})
return true;
}
}
function encode(s) {
return encodeURIComponent(s);
}

function decode(s) {
return decodeURIComponent(s);
}














export default {
getCookie,
setCookie,
delCookie,
goback,
gotoLogin
}



