import util from "@/utils/utils.js";
import service from "@/service/service.js";
export default {
  data() {
    return {
      smdl: true,
      loginForm: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    async submitForm() {
      let that = this;
      if (this.loginForm.username === "" || this.loginForm.password === "") {
        this.$message({
          showClose: true,
          message: "账号或密码不能为空",
          type: "error"
        });
        return false;
      } else {
        // 将 username 设置为 token 存储在 store，仅为测试效果，实际存储 token 以后台返回为准
        const checkLogin = await service.checkLogin({
          username: this.loginForm.username,
          pwd: this.loginForm.password
        });
        console.log(checkLogin.data.token);

        if (checkLogin.data.status == 1) {
          that.$store
            .dispatch("setToken", checkLogin.data.token)
            .then(() => {
              that.$router.push({
                path: "/"
              });
            })
            .catch(res => {
              that.$message({
                showClose: true,
                message: res,
                type: "error"
              });
            });
          // sessionStorage.setItem('token', checkLogin.data.token);
          util.setCookie("token", checkLogin.data.token);
          util.setCookie("admin_id", checkLogin.data.admin.admin_id);
          util.setCookie("username", checkLogin.data.admin.user_name);
        } else {
          this.$message.error(checkLogin.data.info);
        }
      }
    },
    message() {}
  },
  mounted() {
    if (util.getCookie("token")) {
      util.delCookie("token");
    }
  }
};



